<template>
    <el-container>
        <el-main>    
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div style="text-align:right;">
                <el-button type="primary" @click="addCompany">+</el-button>
            </div>     
            <el-form ref="form" :model="form" label-width="180px" class="gap" label-position="right">
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="" prop="cityList">
                            <region v-model="form.cityList" :startLevel="startLevel" :endLevel="endLevel"></region>
                        </el-form-item>
                        <el-form-item label="" prop="cooperationTime">
                            <el-date-picker
                                v-model="form.cooperationTime"
                                type="daterange"
                                align="right"
                                unlink-panels
                                range-separator=""
                                start-placeholder=""
                                end-placeholder=""
                                value-format="yyyy-MM-dd"
                                format="yyyy-MM-dd">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="" prop="businessType">
                            <el-select v-model="form.businessType" filterable>
                                <el-option label="" value="3"></el-option>
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="storeAddress">
                            <el-input v-model="form.storeAddress"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" prop="name">
                            <el-input v-model="form.name"></el-input>
                        </el-form-item>
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="" prop="searchType">
                                    <el-select v-model="form.searchType" filterable>
                                        <el-option label="" value="0"></el-option>
                                        <el-option label="" value="1"></el-option>
                                        <el-option label="" value="2"></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12" v-if="form.searchType==1">
                                <el-form-item prop="searchDay" label-width="0">
                                    <el-input v-model="form.searchDay" placeholder=""></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12" v-if="form.searchType==2">
                                <el-form-item prop="searchDate" label-width="0" >
                                    <el-date-picker
                                        format="yyyy-MM-dd"
                                        v-model="form.timeStart"
                                        style="width:100%;"
                                        type="date"
                                        placeholder=""
                                        value-format="yyyy-MM-dd">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        
                        <el-form-item label="" prop="storeName" >
                            <el-input v-model="form.storeName"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="agency">
                            <el-input v-model="form.agency"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <el-row :gutter="20">
                <el-col :span="2" :offset="11">
                    <el-button class="reset" @click="resetForm"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary" @click="search"></el-button>
                </el-col>
            </el-row>
            <div class="search-result"> {{summary.companyTotal}}{{summary.hATotal}}{{summary.nATotal}}</div>
            <el-table :data="companyInfoList" border style="width: 100%">
                <el-table-column type="index" label="" align="center" width="100" ></el-table-column>
                <el-table-column prop="city" label="" align="center" ></el-table-column>
                <el-table-column prop="companyName" label="" align="center" ></el-table-column>
                <el-table-column prop="companyAbbreviation" label="" align="center" ></el-table-column>
                <el-table-column prop="agencyName" label="" align="center"></el-table-column>
                <el-table-column prop="cooperationStart" label="" align="center"></el-table-column>
                <el-table-column prop="cooperationEnd" label="" align="center"></el-table-column>
                <el-table-column prop="bdName" label="bd" align="center"></el-table-column>
                <el-table-column prop="createTime" label="" align="center"></el-table-column>
                <el-table-column prop="creater" label="" align="center"></el-table-column>
                <el-table-column prop="name" label="" width="300px" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="editorCompany(scope.$index, scope.row)" type="text"> </el-button>
                        <el-button size="mini" @click="bankAccount(scope.$index, scope.row)" type="text"> </el-button>
                        <el-button size="mini"  type="text" @click="handleEnd(scope.$index, scope.row)"></el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="storeNum" label="" align="center"></el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="form.currentPage"
                    :page-sizes="[10, 20, 50, 100,500]"
                    :page-size="form.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="summary.companyTotal">
                </el-pagination>
            </div>
            <el-dialog title="" :visible.sync="firstDialogVisible" width="30%" >
                <p>1</p>
                <p>2</p>
                <p>3</p>
                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="firstDialogVisible = false,secondDialogVisible = true"></el-button>
                </span>
            </el-dialog>
            <el-dialog title="" :visible.sync="secondDialogVisible" width="30%" >
                <textarea name="" id="" rows="10" placeholder="" v-model="remark" style="width:100%;"></textarea>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="continueJoin"> </el-button>
                    <el-button type="primary" @click="endJoin" > </el-button>
                </span>
            </el-dialog>
            <!---->
            <editor-company ref="editor" :companyId="companyId" :title="title" @editSuccess='editSuccess' @addSuccess='addSuccess'></editor-company>
            <!---->
            <commission ref="commission" :companyId="companyId"></commission>
        </el-main>
    </el-container> 
</template>

<script>
import EditorCompany from '@/components/company/_EditorCompany';
import Commission from '@/components/company/_Commission';
import BreadCrumb from '@/components/common/BreadCrumb';
import Region from '@/components/common/Region';

export default {
    name: 'CompanyManagement',
    components:{EditorCompany,Commission,BreadCrumb,Region},
    data () {
        return {
            breadCrumb: [{text:''},{text: ""}],//
            companyId:'',//Id;
            companyInfoList:[],//
            companyInfoIndex:'',//
            currentCompanyInfo:'',//
            endLevel:2,//
            firstDialogVisible: false,//
            remark:'',//
            startLevel:1,//
            secondDialogVisible:false,//
            summary:{},//summary
            title:'',//
            // 
            form:{
                agency:null,//
                businessType:'3',//,1.2.3.
                cityId:null,//Id
                cityList:[],//
                cooperationTime:null,//
                currentPage:1,//1
                pageSize:10,//10
                name:null,//
                searchDate:null,//
                searchDay:null,// 
                searchType:null,//
                storeAddress:null,//
                storeName:null//
            }
        }
    },
    created(){
        this.requestList();
    },
    methods:{
        // ;
        addSuccess(addInfo){
            // ;
            this.$refs.form.resetFields();
            this.form.pageSize=10;
            this.form.currentPage=1;
            this.requestList();
        },
        resetForm(formName) {
            this.$refs.form.resetFields();
        },
        //
        search(){
            this.requestList();
        },
        //
        handleSizeChange(val) {
            this.form.pageSize=val;
            // 
            this.requestList();
        },
        //
        handleCurrentChange(val) {
            this.form.currentPage=val;
            //
            this.requestList();
        },
        // 
        addCompany(){
            //  
            this.title='';            
            setTimeout(()=>{
                this.$refs.editor.open();
            },200); 
        },
        //
        editorCompany(index, row){
            //;
            this.companyInfoIndex=(this.form.currentPage-1)*this.form.pageSize+index;
            // ;
            this.companyId=row.companyId;
            this.title='';
            // ,setTimeout;
            setTimeout(()=>{
                this.$refs.editor.open();
            },200);  
        },
        // ;
        editSuccess(editInfo){
            // this.companyInfoList.splice(this.companyInfoIndex,1,editInfo);
            this.requestList();
        },
        // 
        bankAccount(index, row){
            this.companyId=row.comapnyId;
            this.$refs.commission.open();
        },
        //,
        handleEnd(index,row){
            this.firstDialogVisible = true,
            this.companyId=row.companyId;
            this.remark='';
        },
        //,
        endJoin(){
            this.secondDialogVisible = false;
            this.$http.post(this.$apiUrl.company.terminate+"/"+this.companyId+"?remark="+this.remark)
                .then(function(data){
                    console.log(data,'');
                })
                .catch(function(err){
                    console.log(err);
                });
        },
        //
        continueJoin(){
            this.secondDialogVisible = false;
            this.remark='';
        },
        // 
        requestList(){
            let self=this;
            // ;
            if(this.form.cityList.length){
                this.form.cityId=this.form.cityList[1];
            };
            let realForm=Object.assign({},this.form);
            delete realForm.cityList;//cityList
            delete realForm.searchType;//
            // ;
            this.$http.post(this.$apiUrl.company.list,realForm)
                .then(function(data){
                    self.companyInfoList=data.data.data.data;
                    console.log(data,'12');
                })
                .catch(function(err){
                    console.log(err,'');
                });
            // summary;
            this.$http.post(this.$apiUrl.company.summary,realForm)
                .then(function(data){
                    self.summary=data.data.data;
                })
                .catch(function(err){
                    console.log(err);
                });
        }
    }
}
</script>

<style scoped>
    .el-select{
        width:100%;
    }
    .el-form-item .el-form-item__label {
        text-align: right;
    }
    .el-date-editor.el-range-editor.el-input__inner.el-date-editor--daterange{
        width:100%;
    }
    .search-result{
        color:gray;
        margin-bottom: 20px;
        margin-top: 50px;
    }
    .pagination{
        margin-top: 100px;
        text-align: center;
    }
    .el-button{
        margin-left: 0 !important;
    }
    .el-dialog__footer{
        text-align: right;
    }
</style>





