<template>
    <el-container>
        <el-main>    
            <bread-crumb :items="breadCrumb"></bread-crumb> 
            <div style="text-align:right;">
                <el-button type="primary" @click="addStore">+</el-button>
            </div>      
            <el-form ref="form" :model="form" label-width="180px" class="gap">
                <el-row >
                    <el-col :span="12">
                        <el-form-item label="" prop="cityList">
                            <region v-model="form.cityList" :startLevel="startLevel" :endLevel="endLevel"></region>
                        </el-form-item>
                        <el-form-item label="" prop="agency">
                            <el-input v-model="form.agency"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="storeType">
                            <el-select v-model="form.storeType" filterable>
                                <el-option label="" value="0"></el-option>
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" prop="storeName">
                            <el-input v-model="form.storeName"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="cooperationTime">
                            <el-date-picker
                                v-model="form.cooperationTime"
                                type="daterange"
                                align="right"
                                unlink-panels
                                range-separator=""
                                start-placeholder=""
                                end-placeholder=""
                                value-format="yyyy-MM-dd"
                                format="yyyy-MM-dd">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="" prop="companyName">
                            <el-input v-model="form.companyName"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <el-row :gutter="20">
                <el-col :span="2" :offset="11">
                    <el-button class="reset" @click="resetForm"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary" @click="search"></el-button>
                </el-col>
            </el-row>
            <div class="search-result"> {{summary.storeTotal}}{{summary.agentTotal}}{{summary.hASTotal}}{{summary.nASTotal}}</div>
            <el-table :data="storeInfo.data" border style="width: 100%">
                <el-table-column prop="storeId" label="id" align="center" ></el-table-column>
                <el-table-column prop="storeName" label="" align="center" ></el-table-column>
                <el-table-column prop="storeTypeName" label="" align="center" ></el-table-column>
                <el-table-column prop="agentCount" label="" align="center" ></el-table-column>
                <el-table-column prop="agencyName" label="" align="center"></el-table-column>
                <el-table-column prop="companyName" label="" align="center"></el-table-column>
                <el-table-column prop="cityName" label="" align="center"></el-table-column>
                <el-table-column prop="createTime" label="" align="center"></el-table-column>
                <el-table-column label="" width="300px" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="editStore(scope.$index, scope.row)" type="text"> </el-button>
                        <el-button size="mini" @click="qrCode(scope.$index, scope.row)" type="text"> </el-button>
                        <el-button size="mini"  type="text" @click="handleEnd(scope.$index, scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="form.currentPage"
                    :page-sizes="[10, 20, 50, 100,500]"
                    :page-size="form.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="storeInfo.total">
                </el-pagination>
            </div>
            <!---->
                <el-dialog title="" :visible.sync="qrCodeShow" width="30%" >
                    <div style="text-align:center;">
                        <img :src="qrCodeInfo.url" alt="">
                    </div>
                </el-dialog>
            <!---->
            <el-dialog title="" :visible.sync="firstDialogVisible" width="30%" >
                <p>1app</p>
                <p>2</p>
                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="firstDialogVisible = false,secondDialogVisible = true"></el-button>
                </span>
            </el-dialog>
            <el-dialog title="" :visible.sync="secondDialogVisible" width="30%" >
                <textarea name="" id="" rows="10" placeholder="" v-model="remark" style="width:100%;"></textarea>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="continueJoin"> </el-button>
                    <el-button type="primary" @click="endJoin" > </el-button>
                </span>
            </el-dialog>
            <!---->
            <editor-store ref="editor"  :title="title" :storeId="storeId" @editSuccess='editSuccess' @addSuccess='addSuccess' :currentStoreInfo="currentStoreInfo"></editor-store>
        </el-main>
    </el-container> 
</template>

<script>
import BreadCrumb from '@/components/common/BreadCrumb';
import EditorStore from '@/components/store/_EditorStore';
import Region from '@/components/common/Region';

export default {
    name: 'StoreManagement',
    components:{BreadCrumb,EditorStore,Region},
    data () {
        return {
            breadCrumb: [{text:''},{text: ""}],
            startLevel:1,//
            endLevel:2,//
            qrCodeShow:false,
            qrCodeInfo:{},//;
            firstDialogVisible: false,//
            secondDialogVisible:false,//
            remark:'',//
            companyInfoIndex:'',//
            currentStoreInfo:'',//
            storeInfo:{},//
            storeId:'',//Id;
            summary:{},//summary
            title:'',//
            // 
            form: {
                agency:null,//
                cityId:null,//Id
                cityList:[],//
                companyName:null,//
                cooperationTime:null,//;
                cooperationStart:null,//
                cooperationEnd:null,//
                currentPage:1,//1
                pageSize:10,//10
                storeName: null,//
                storeType:'0',//
            }  
        }
    },
    created(){
        this.requestList();
    },
    methods:{
        // ;
        addSuccess(addInfo){
            this.$refs.form.resetFields();
            this.form.pageSize=10;
            this.form.currentPage=1;
            this.requestList();
            console.log('');
        },
        // ;
        editSuccess(editInfo){
            // ;
            this.requestList();
        },
        resetForm(formName) {
            this.$refs.form.resetFields();
        },
        //
        search(){
            this.requestList();
        },
        //
        handleSizeChange(val) {
            this.form.pageSize=val;
            this.requestList();
        },
        //
        handleCurrentChange(val) {
            this.form.currentPage=val;
            this.requestList();
        },
        // 
        addStore(){
            //  
            this.title='';
            setTimeout(()=>{
                this.$refs.editor.open();
            },200); 
        },
        //
        editStore(index, row){
            // ;
            this.storeId=row.storeId; 
            this.title='';
            // ,setTimeout;
            setTimeout(()=>{
                this.$refs.editor.open();
            },200);  
        },
        //
        qrCode(index, row){
            let self=this;
            this.storeId=row.storeId;
            this.$http.get(this.$apiUrl.store.qrcode+"/"+this.storeId)
                .then(function(data){
                    self.qrCodeInfo=data.data.data
                    console.log(self.qrCodeInfo,'');
                })
                .catch(function(err){
                    console.log(err);
                })
            this.qrCodeShow=true;
        },
        //,
        handleEnd(index,row){
            this.storeId=row.storeId;
            console.log(row,1111111111)
            this.firstDialogVisible=true;
        },
        //,
        endJoin(){
            let self=this;
            if(this.remark){
                this.secondDialogVisible=false;
                this.$http.post(this.$apiUrl.store.terminate+"/"+this.storeId+"?remark="+this.remark)
                    .then(function(data){
                        self.remark="";
                        console.log(data);
                    })
                    .catch(function(err){
                        console.log(err);
                    })
            }else{
                alert('');
            };
            //;
        },
        //
        continueJoin(){
            this.secondDialogVisible=false;
            this.remark='';
        },
        // ;
        requestList(){
            let self=this;
            // ;
            if(this.form.cityList.length){
                this.form.cityId=this.form.cityList[1];
            };
            // ;
            this.$http.post(this.$apiUrl.store.list,this.form)
                .then(function(data){
                    self.storeInfo=data.data.data;
                    console.log(self.storeInfo,'');
                })
                .catch(function(err){
                    console.log(err);
                });
            // summary;
            this.$http.post(this.$apiUrl.store.summary,this.form)
                .then(function(data){
                    self.summary=data.data.data
                    console.log(self.summary,'summary');
                })
                .catch(function(err){
                    console.log(err);
                })
        }
    }
}
</script>

<style scoped>
    .search-result{
        color:gray;
        margin-bottom: 20px;
        margin-top: 50px;
    }
    .pagination{
        margin-top: 100px;
        text-align: center;
    }
    .el-button{
        margin-left: 0 !important;
    }
</style>





