<template>
    <el-container>
        <el-main>    
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div class="hd"> </div>
            <div>{{operateRemark}}</div>
            <div class="gap-2">
                <el-button style="width: 140px" type="danger" @click="handleReject"></el-button>
                <el-button style="width: 140px" type="primary" @click="handleApprove"></el-button>
                <el-button style="width: 140px" type="" @click="handleClose"></el-button>
            </div>
            <h2 class="gap-2"></h2>
            <el-table :data="historyRecords" stripe style="width: 100%; margin-bottom: 40px;">
                <el-table-column prop="submitTime" label="" width="180"></el-table-column>
                <el-table-column prop="auditTime" label="" width="180"></el-table-column>
                <el-table-column prop="auditState" label=""></el-table-column>
                <el-table-column prop="remark" label=""></el-table-column>
                <el-table-column prop="auditor" label=""></el-table-column>
            </el-table> 
            <el-dialog :title="title" :visible.sync="dialogVisible" width="30%">
                <textarea name="" id="" style="width:100%;" rows="10" placeholder="" v-model="remark"></textarea>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false"> </el-button>
                    <el-button type="primary" @click="dialogVisible = false,submit()"> </el-button>
                </span>
            </el-dialog>
        </el-main>
    </el-container> 
</template>

<script>
import BreadCrumb from '@/components/common/BreadCrumb';

export default {
    name:'ProfessionEndVerify',
    components:{BreadCrumb},
    data(){
        return {
            breadCrumb: [{text:''},{text: ""},{text:''}],
            historyRecords:[],// 
            dialogVisible:false,
            operateRemark:'',//
            remark:'',//
            title:'',
            targetId:this.$route.query.targetId,
            targetType:this.$route.query.targetType
        }
    },
    created(){
        let self=this;
        // ;
        this.$http.get(this.$apiUrl.professionEnd.endDetail+"?targetId="+this.targetId+"&targetType="+this.targetType)
            .then(function(data){
                self.operateRemark=data.data.data.operateRemark;
            })
            .catch(function(err){
                console.log(err,'');
            });
            let form={
                targetId:this.targetId,
                targetType:this.targetType
            }
        // 
        this.$http.post(this.$apiUrl.professionEnd.historyRecords,form)
            .then(function(data){
                //
                self.historyRecords=data.data.data;
            })
            .catch(function(err){
                console.log(err);
            });
    },
    methods:{
        handleReject(){
            this.dialogVisible=true;
            this.title='';
        },
        handleApprove(){
            this.dialogVisible=true;
            this.title='';
        },
        handleClose(){
            history.back();
        },
        submit(){
            let form={
                targetId:this.targetId,
                targetType:this.targetType,
                remark:this.remark
            };
            if(this.title==''){
                this.$http.post(this.$apiUrl.professionEnd.reject,form)
                .then(function(data){
                    this.remark="";
                })
                .catch(function(err){
                    console.log(err);
                })
            }else{
                this.$http.post(this.$apiUrl.professionEnd.adopt,form)
                .then(function(data){
                    this.remark="";
                })
                .catch(function(err){
                    console.log(err);
                })
            }  
        }
    }
}
</script>

<style scoped>
    .hd{
        margin: 20px 0;
    }
    .skip{
        color:blue;
    }
</style>





