<template>
    <el-dialog  :visible.sync="dialogVisible" width="50%">
        <el-form  label-width="120px">
            <el-form-item label="">
                <span>{{form.name}}</span>
            </el-form-item>
            <el-form-item label="">
                <span v-if="form.storeType==1"></span>
                <span v-if="form.storeType==2"></span>
            </el-form-item>
            <el-form-item label="" v-if="form.storeType==1" >
                <span>{{form.agencyName}}</span>
            </el-form-item>
            <el-form-item label="" v-if="form.storeType==2" >
                <span>{{form.companyName}}</span>
            </el-form-item>
            <el-form-item label=":">
                <span>{{form.address}}</span>
            </el-form-item>
            <el-form-item label="--:">
                <span>{{form.cityName}}-{{form.districtIdName}}-{{form.townIdName}}</span>
            </el-form-item>
            <el-form-item label=":">
                <span>{{form.longlat}}</span>
            </el-form-item>
        </el-form>
    </el-dialog>
</template>

<script>
export default {
    name:'storeDetail',
    data(){
        return {
            dialogVisible:false,
            form: {
                address:'',//;
                agencyId:'',//Id;
                cityId:'',//Id;
                companyId:'',//Id;
                districtId:'',//Id;
                longlat:'',//;
                name:'',//;
                provinceId:'',//Id;
                storeType:'',//;
                townId:'',//Id;
            }
        }
    },
    methods:{
        open(storeId) {
            let self =this;
            // ;
            this.$http.get(this.$apiUrl.store.add+"?id="+storeId)
                .then(function(data){
                    self.form=data.data.data;
                    console.log(self.form,'');
                })
                .catch(function(err){
                    console.log(err);
                })
            this.dialogVisible = true;
        }
    }
}
</script>

<style>

</style>




