<template>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
        <el-form :model="form" :rules="rules"  ref="form" label-width="110px" class="demo-form">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="openBankType" class="tl">
                        <el-input v-model="form.openBankType"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="openBankBranch">
                        <el-input v-model="form.openBankBranch"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="accountName" class="tl">
                        <el-input v-model="form.accountName"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="bankAccount">
                        <el-input v-model="form.bankAccount"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
            <span slot="footer" class="dialog-footer">
            <el-button @click="submitForm" type="primary"></el-button>
            <el-button @click="resetForm"></el-button>
        </span> 
    </el-dialog>
</template>

<script>
export default {
    name:'commission',
    props:['companyId'],
    data(){
        return {
            dialogVisible:false,
            form:{
                bankAccount:'',//
                openBankType:'',// 
                accountName: '',//
                openBankBranch: '',// 
            },
            rules: {
                openBankType:[{ required: true, message: '', trigger: 'blur' }],
                openBankBranch: [{ required: true, message: '', trigger: 'blur' }],
                accountName: [{ required: true, message: '', trigger: 'blur' },],
                bankAccount:[{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    methods:{
        submitForm() {
            let self=this;
            this.$refs.form.validate((valid) => {
                if (valid) {
                    let realForm={};
                    realForm.bankAccount=this.form.bankAccount;
                    realForm.openBankType=this.form.openBankType;
                    realForm.accountName=this.form.accountName;
                    realForm.openBankBranch=this.form.openBankBranch;
                    realForm.companyId=this.companyId;
                    this.dialogVisible=false;
                    // ,Id;
                    this.$http.post(this.$apiUrl.company.commission,realForm)
                        .then(function(data){
                            alert('');
                            console.log(data,'');
                            self.$refs.form.resetFields();
                        })
                        .catch(function(err){
                            console.log(err,'');
                        });
                    // 
                    // this.$refs.form.resetFields();
                } else {
                    alert('')
                    return false;
                }
            });
        },
        resetForm() {
            this.$refs.form.resetFields();
            this.dialogVisible=false;
        },
        handleClose(done) {
            // x;
            this.$confirm('')
            .then(_ => { 
                // 
                this.$refs.form.resetFields();
                done();
            })
            .catch(_ => {
                //
            });
        },
        // ;
        open(){
            this.dialogVisible=true;
            console.log(this.companyId,123456678899)
            // ;
            let self=this;
            this.$http.get(this.$apiUrl.company.detail+"?companyId="+this.companyId)
                .then(function(data){
                    self.form=data.data.data;
                    console.log(self.form,'');
                })
                .catch(function(err){
                    console.log(err);
                });
        }
    }
}
</script>

<style scoped>
    .el-dialog__footer{
        text-align: left;
    }
</style>





