<template>
    <el-container>
        <el-main>    
            <bread-crumb :items="breadCrumb"></bread-crumb>
            <div style="text-align:right;">
                <el-button type="primary" @click="addAgent"></el-button>
            </div>     
            <el-form ref="form" :model="form" label-width="180px" class="gap" label-position="right">
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="" prop="cityList">
                            <region v-model="form.cityList" :startLevel="startLevel" :endLevel="endLevel"></region>
                        </el-form-item>
                        <el-form-item label="" prop="agencyState">
                            <el-select v-model="form.agencyState" filterable>
                                <el-option label="" value="0"></el-option>
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                                <el-option label="" value="3"></el-option>
                                <el-option label="" value="4"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="cooperationTime">
                            <el-date-picker
                                v-model="form.cooperationTime"
                                type="daterange"
                                align="right"
                                unlink-panels
                                range-separator=""
                                start-placeholder=""
                                end-placeholder=""
                                value-format="yyyy-MM-dd"
                                format="yyyy-MM-dd">
                            </el-date-picker>
                        </el-form-item>
                        <el-form-item label="" prop="agencyType">
                            <el-select v-model="form.agencyType" filterable>
                                <el-option label="" value="0"></el-option>
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="" prop="searchType">
                                    <el-select v-model="form.searchType" filterable>
                                        <el-option label="" value="0"></el-option>
                                        <el-option label="" value="1"></el-option>
                                        <el-option label="" value="2"></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12" v-if="form.searchType==1">
                                <el-form-item prop="searchDay" class="expire" label-width="0">
                                    <el-input v-model="form.searchDay" placeholder=""></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12" v-if="form.searchType==2">
                                <el-form-item prop="searchDate" class="expire" label-width="0">
                                    <el-date-picker
                                        format="yyyy-MM-dd"
                                        v-model="form.timeStart"
                                        type="date"
                                        placeholder=""
                                        style="width:100%;"
                                        value-format="yyyy-MM-dd">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-form-item label="" prop="agencyTag" v-if="form.agencyState==0||form.agencyState==3">
                            <el-select v-model="form.agencyTag" filterable>
                                <el-option label="" value="0"></el-option>
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                                <el-option label="" value="3"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="agencyName">
                            <el-input v-model="form.agencyName"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <el-row :gutter="20">
                <el-col :span="2" :offset="11">
                    <el-button class="reset" @click="reset"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary" @click="search"></el-button>
                </el-col>
            </el-row>
            <div class="search-result"> {{summary.agencyTotal}}{{summary.regionTotal}}{{summary.storeTotal}}</div>
            <el-table :data="agencyInfo.data" border style="width: 100%">
                <el-table-column prop="agencyCompanyName" label="" align="center" ></el-table-column>
                <el-table-column prop="cityName" label="" align="center"></el-table-column>
                <el-table-column prop="agencyTypeName" label="" align="center"></el-table-column>
                <el-table-column prop="regionTotal" label="" align="center"></el-table-column>
                <el-table-column prop="storeTotal" label="" align="center"></el-table-column>
                <el-table-column prop="agencyStateName" label="" align="center"></el-table-column>
                <el-table-column prop="startTime" label="" align="center"></el-table-column>
                <el-table-column prop="endTime" label="" align="center"></el-table-column>
                <el-table-column label="" width="300px" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="detail(scope.$index, scope.row)" v-if="scope.row.agencyState!=2" type="text"></el-button>
                        <!--<el-tooltip placement="right" effect="light" v-if="scope.row.agencyState!=2">
                            <el-button type="text" size="mini"></el-button>
                            <div slot="content" @click="edit(scope.$index,scope.row)" v-if="scope.row.agencyState!=1"></div>
                            <div slot="content" class="cz" @click="followUp(scope.$index,scope.row)"></div>
                            <div slot="content" class="cz" @click="endJoin(scope.$index,scope.row)" v-if="scope.row.agencyState==4"></div>
                        </el-tooltip>-->
                        <el-dropdown v-if="scope.row.agencyState!=2"trigger="click">
                            <span class="el-dropdown-link" style="color:#409EFF;font-size:12px;"><i class="el-icon-arrow-down el-icon--right"></i></span>
                            <el-dropdown-menu slot="dropdown" >
                                <el-dropdown-item @click.native="edit(scope.$index,scope.row)" v-if="scope.row.agencyState!=1"></el-dropdown-item>
                                <el-dropdown-item @click.native="followUp(scope.$index,scope.row)"></el-dropdown-item>
                                <el-dropdown-item @click.native="endJoin(scope.$index,scope.row)" v-if="scope.row.agencyState==4"></el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                        <el-button v-if="scope.row.agencyState==2" size="mini" @click="reSubmit(scope.$index, scope.row)" type="text"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="form.currentPage"
                    :page-sizes="[10, 20, 50, 100,500]"
                    :page-size="form.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="agencyInfo.total">
                </el-pagination>
            </div>
            <el-dialog title="!" :visible.sync="firstDialogVisible" width="30%" >
                <p>{{agencySotre}},</p>
                <p>a</p>
                <p>b,</p>
                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="firstDialogVisible = false,secondDialogVisible = true"></el-button>
                </span>
            </el-dialog>
            <el-dialog title="!,," :visible.sync="secondDialogVisible" width="30%" >
                <textarea name="" id="" rows="10" placeholder="" v-model="remark" style="width:100%;"></textarea>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="continueJoin"> </el-button>
                    <el-button type="primary" @click="noJoin" > </el-button>
                </span>
            </el-dialog>
        </el-main>
    </el-container> 
</template>

<script>
import BreadCrumb from '@/components/common/BreadCrumb';
import Region from '@/components/common/Region';

export default {
    name: 'AgentManagement',
    components:{BreadCrumb,Region},
    data () {
        return {
            agencyInfo:{},//
            agencySotre:'',//
            breadCrumb: [{text:''},{text: ""}],//
            companyInfoIndex:'',//
            currentCompanyInfo:'',//
            endLevel:2,//
            firstDialogVisible: false,//
            remark:'',//
            secondDialogVisible:false,//
            startLevel:1,//
            summary:{},//summary
            title:'',//
            // 
            form: {
                agencyName:null,//
                agencyState:'0',//
                agencyTag:'0',//
                agencyType:'0',//
                cityId:null,//
                cityList:[],//
                cooperationTime:null,//
                currentPage:1,//1
                pageSize:10,//10
                searchDate:null,//
                searchDay:null,//
                searchType:null,//
            }  
        }
    },
    created(){
        this.requestList();
    },
    methods:{
        // 
        edit(index,row){
            this.$router.push({name:'EditAgent',params:{id:row.agencyId}});
        },
        // 
        followUp(index,row){
            this.$router.push({name:'FollowUp',query:{agencyId:row.agencyId}});
        },
        // ;
        endJoin(index,row){
            this.currentCompanyInfo=row;
            this.agencySotre=this.currentCompanyInfo.storeTotal;
            this.firstDialogVisible=true;
        },
        // ;
        noJoin(){
            let self=this;
            if(this.remark){
                this.secondDialogVisible = false;
                this.$http.post(this.$apiUrl.agent.terminate+"/"+this.currentCompanyInfo.agencyId+"?remark="+this.remark)
                    .then(function(data){
                        console.log(data,'');
                        self.remark='';
                        alert('');
                    })
                    .catch(function(err){
                        console.log(err);
                    });
            }else{
                alert('');
            }
            
        },
        // 
        detail(index,row){
            this.$router.push({name:'AgentDetail',params:{id:row.agencyId}});
        },
        // 
        reSubmit(index,row){
            this.$router.push({name:'EditAgent',params:{id:row.agencyId}});
        },
        // 
        reset(){
            this.$refs.form.resetFields();
        },
        //
        search(){
            this.requestList();
        },
        //
        handleSizeChange(val) {
            if(val!=this.form.pageSize){
                this.form.pageSize=val;
                this.requestList();
            }
        },
        //
        handleCurrentChange() {
            if(val!=this.form.currentPage){
                this.form.currentPage=val;
                this.requestList();
            }
        },
        // 
        addAgent(){
             this.$router.push({path:"/agent/createAgent"});
        },
        //
        continueJoin(){
            this.secondDialogVisible = false;
            this.remark='';
        },
        // ;
        requestList(){
            let self=this;
            // ;
            if(this.form.cityList.length){
                this.form.cityId=this.form.cityList[1];
            };
            // 03null;
            let isTag=this.form.agencyState==0||this.form.agencyState==3;
            if(!isTag){
                this.form.agencyTag="0";
            };
            //dataform,;
            let realForm=Object.assign({},this.form);
            delete realForm.cityList;
            delete realForm.searchType;
            // ;
            this.$http.post(this.$apiUrl.agent.list,realForm)
                .then(function(data){
                    self.agencyInfo=data.data.data;
                })
                .catch(function(err){
                    console.log(err,'');
                });
            // summary;
            this.$http.post(this.$apiUrl.agent.summary,realForm)
                .then(function(data){
                    self.summary=data.data.data;
                })
                .catch(function(err){
                    console.log(err);
                });
        }
    }
}
</script>

<style scoped>
    .el-select{
        width:100%;
    }
    .el-date-editor.el-range-editor.el-input__inner.el-date-editor--daterange{
        width:100%;
    }
    .search-result{
        color:gray;
        margin-bottom: 20px;
        margin-top: 50px;
    }
    .pagination{
        margin-top: 100px;
        text-align: center;
    }
    .el-button{
        margin-left: 0 !important;
    }
    .el-dialog__footer{
        text-align: right;
    }
    .cz{
        margin-top: 10px;
    }
</style>





